import React, {useEffect} from "react";
import styled from "styled-components";
import {useNavigate} from "react-router-dom";
import HolidaysTimerLogistics from "../components/HolidaysTimerLogistics.jsx";


export const VIEWER_DOCUMENT_SESSION_STORAGE = 'viewer_data_object';
export default function ViewerDocumentScreen() {
    const history = useNavigate();
    useEffect(() => {
        if (sessionStorage.getItem(VIEWER_DOCUMENT_SESSION_STORAGE) === null)
            history(-1)
    }, [])
    const {link} = JSON.parse(sessionStorage.getItem(VIEWER_DOCUMENT_SESSION_STORAGE))
    return (
        <HeaderViewer/>
    )
}

function HeaderViewer() {
    const history = useNavigate()
    const {title} = JSON.parse(sessionStorage.getItem(VIEWER_DOCUMENT_SESSION_STORAGE))
    return (
        <HeaderViewerBox>
            <HolidaysTimerLogistics/>
            <div className={`title`}>{title}</div>
            <div className={`action`}>
                <button onClick={() => {
                    sessionStorage.removeItem(VIEWER_DOCUMENT_SESSION_STORAGE);
                    history(-1)
                }}>
                    Закрыть
                </button>
            </div>
        </HeaderViewerBox>
    )
}

const HeaderViewerBox = styled.div`
  display: flex;
  position: relative;
  flex-direction: row;
  width: 100%;
  background-color: #2f4858;
  height: 70px;
  justify-content: center;
  align-items: center;

  & > div.title {
    display: flex;
    margin-left: 1em;
    font-size: 20px;
    color: #FFFFFF;
    flex: 12;
  }

  & > div.action {
    display: flex;
    flex: 2;
    flex-direction: row;
    justify-content: flex-end;

    & > button {
      margin: 5px;
      border: none;
      font-size: 16px;
      cursor: pointer;
      color: #FFFFFF;
      background-color: rgb(33, 150, 83);
      text-decoration: none;
      padding: 10px 20px;
      border-radius: 4px;
      transition: all 0.2s ease;

      &:hover {
        transform: scale(1.01);
        filter: drop-shadow(3px 3px 5px rgba(33, 150, 83, 0.5));
      }

      &:active {
        transform: scale(0.99);
        filter: drop-shadow(3px 3px 5px rgba(33, 150, 83, 0.15));
      }
    }
  }
`;